import { connect } from 'react-redux';
import { deleteTrans } from 'modules/transactions/actions';
import {
    selectReadWrite,
    transactionIsEmpty,
    selectThIsBusy,
} from 'modules/transactions/selectors';
import RevertButton from './RevertButton';

const mapStateToProps = (state) => {
    const th = selectReadWrite(state);
    const enableRevert = th !== undefined && !selectThIsBusy(state) && !transactionIsEmpty(state);
    return { enableRevert, th };
};

const mapDispatchToProps = dispatch => ({
    onRevert(th) {
        dispatch(deleteTrans(th));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(RevertButton);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
