import React from 'react';
import PropTypes from 'prop-types';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import style from './RollbackStyles';

// FIXME(dbildstr): fix no-static-element-interactions warning
/* eslint-disable jsx-a11y/no-static-element-interactions */
const buildRollback = (rollback, index, onSelectItem, selectedIndex) => (
    <div
        key={`${rollback.date}-${rollback.filename}`}
        style={selectedIndex === index ? style.selectedRow : style.row}
        onClick={() => { onSelectItem(index, rollback); }}
        >
        <div>
            <span>
                {rollback.date} - {rollback.creator} / {rollback.via}
            </span>
        </div>
    </div>
);
/* eslint-enable jsx-a11y/no-static-element-interactions */
const getRollbackFile = (selectedIndex, rollbacks) => (
    selectedIndex !== -1 && selectedIndex < rollbacks.length
        ? rollbacks[selectedIndex].rollback
        : ''
);

const getRollbackList = (rollbacks, selectedIndex, selectItem) => (
    rollbacks.length > 0
        ? rollbacks.map((rollback, index) => (
            buildRollback(rollback, index, selectItem, selectedIndex)
        ))
        : <div>No rollbacks found</div>
);

const Rollback = ({
    rollbacks,
    onChangeKeypath,
    onSelective,
    selectItem,
    selective,
    selectedIndex,
}) => (
    <div>
        <div style={style.wrapper}>
            {getRollbackList(rollbacks, selectedIndex, selectItem)}
        </div>
        <div key="rollback-keypath" style={style.keypathWrapper}>
            <div style={style.keypath}>
                <div>Keypath:</div>
                <div>
                    <input
                        type="text"
                        placeholder="Keypath"
                        onChange={onChangeKeypath}
                    />
                </div>
            </div>
            {selectedIndex !== -1 && <div style={style.selectiveWrapper}><CheckBox
                onSetValue={onSelective}
                value={selective}
                styleName="listStyles"
                >
                <span style={style.checkboxSpan}>Selective</span>
            </CheckBox></div>}
        </div>
        <div
            style={selectedIndex !== -1 ?
                style.rollbackList :
                style.rollbackList.hidden}
        >
            <pre>
                {getRollbackFile(selectedIndex, rollbacks)}
            </pre>
        </div>
    </div>
);

Rollback.propTypes = {
    onChangeKeypath: PropTypes.func,
    onSelective: PropTypes.func,
    selectItem: PropTypes.func,
    selective: PropTypes.bool,
    selectedIndex: PropTypes.number,
    rollbacks: PropTypes.arrayOf(
        PropTypes.shape({
            comment: PropTypes.string,
            creator: PropTypes.string,
            date: PropTypes.string,
            filename: PropTypes.string,
            label: PropTypes.string,
            nr: PropTypes.number,
            via: PropTypes.string,
        }),
    ),
};

export default Rollback;
