import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import {
    selectNativeCommitDryRunResult,
    selectReadWrite,
    selectThIsBusy,
    transactionIsEmpty,
} from 'modules/transactions/selectors';
import {
    nativeCommitDryRun,
} from 'modules/transactions/actions';

import style from './CommitManagerStyles';
import NativeCommitDryRunTab from './NativeCommitDryRunTab';

const loader = <div style={style.loaderbox}><Loader color={defaultStyles.color9} /></div>;

class NativeCommitDryRunTabContainer extends React.Component {
    componentDidMount() {
        const {
            th,
            emptyTransaction,
        } = this.props;
        this.shouldCallDryRun = !emptyTransaction && !!th;
        if (this.shouldCallDryRun) {
            this.onNativeCommitDryRun();
        }
    }

    componentDidUpdate() {
        this.onNativeCommitDryRun();
    }

    componentWillUnmount() {
        this.shouldCallDryRun = false;
    }

    onNativeCommitDryRun() {
        const {
            onNativeCommitDryRun,
            thIsBusy,
            th,
        } = this.props;

        if (this.shouldCallDryRun
            && !thIsBusy
            && th) {
            this.shouldCallDryRun = false;
            onNativeCommitDryRun(th);
        }
    }

    render() {
        const { thIsBusy, nativeResult } = this.props;
        const { device: devices } = nativeResult;

        if (thIsBusy) {
            return loader;
        }

        return (
            <div data-handle="NativeCommitDryRunContainer">
                <NativeCommitDryRunTab devices={devices} />
            </div>
        );
    }
}

NativeCommitDryRunTabContainer.defaultProps = {
    nativeResult: {},
};

NativeCommitDryRunTabContainer.propTypes = {
    nativeResult: PropTypes.oneOfType([
        PropTypes.shape(Object),
    ]),
    onNativeCommitDryRun: PropTypes.func,
    thIsBusy: PropTypes.bool,
    th: PropTypes.number,
    emptyTransaction: PropTypes.bool,
};

const mapDispatchToProps = dispatch => ({
    onNativeCommitDryRun(th) {
        dispatch(nativeCommitDryRun(th));
    },
});

const mapStateToProps = state => ({
    nativeResult: selectNativeCommitDryRunResult(state),
    thIsBusy: selectThIsBusy(state),
    th: selectReadWrite(state),
    emptyTransaction: transactionIsEmpty(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(NativeCommitDryRunTabContainer);

export {
    NativeCommitDryRunTabContainer as _NativeCommitDryRunTabContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
