import React from 'react';
import style from './CommitManagerStyles';
import { Link } from 'react-router';
import { getLinkWithPath } from 'ConfigurationEditor';
import { escapeKey } from '@webui-one/core/utils/path';

export const renderRow = (item, key) => (
    <tr style={!(key % 2 === 0) ? style.table.tr : null} key={key}>
        <td style={style.table.td}>
            <Link
                style={style.table.a}
                to={getLinkWithPath(escapeKey(item.path))}
            >
                {item.path}
            </Link>
        </td>
        <td style={style.table.td}>{item.op}</td>
        <td style={style.table.td}>{item.old}</td>
        <td style={style.table.td}>{item.value}</td>
    </tr>
);

export const renderHeader = () => (
    <thead style={style.table.thead}>
        <tr>
            <th style={style.table.th}>Path</th>
            <th style={style.table.th}>Operation</th>
            <th style={style.table.th}>Old value</th>
            <th style={style.table.th}>New value</th>
        </tr>
    </thead>
);

export const renderBody = changes => (
    <tbody style={style.table.tbody}>
        { changes.map((change, ix) => (renderRow(change, ix))) }
    </tbody>
);

export {
    renderBody as _renderBody,
    renderRow as _renderRow,
    renderHeader as _renderHeader,
};
