import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import {
    selectCommitDryRunResult,
    selectCommitDryRunError,
    selectReadWrite,
    selectHasLatestCommitDryRunResult,
    selectThIsBusy,
    transactionIsEmpty,
} from 'modules/transactions/selectors';
import {
    commitDryRun,
} from 'modules/transactions/actions';

import style from './CommitManagerStyles';
import commitDryRunTab from './CommitDryRunTab';

const loader = <div style={style.loaderbox}><Loader color={defaultStyles.color9} /></div>;

class CommitDryRunTabContainer extends React.Component {
    componentDidMount() {
        this.onCommitDryRun();
    }

    componentDidUpdate() {
        this.onCommitDryRun();
    }

    onCommitDryRun() {
        const {
            onCommitDryRun,
            th,
            emptyTransaction,
            hasLatestCommitDryRunResult,
            thIsBusy,
            commitDryRunError,
        } = this.props;

        const hasNotError = !(commitDryRunError && commitDryRunError.message);

        if (
            !emptyTransaction &&
            !!th &&
            !hasLatestCommitDryRunResult &&
            !thIsBusy &&
            hasNotError
        ) {
            onCommitDryRun(th);
        }
    }

    render() {
        const { thIsBusy, result } = this.props;
        if (thIsBusy) {
            return loader;
        }
        return (
            commitDryRunTab(result)
        );
    }
}

CommitDryRunTabContainer.defaultProps = {
    result: {},
};

CommitDryRunTabContainer.propTypes = {
    commitDryRunError: PropTypes.shape({
        message: PropTypes.string,
    }),
    thIsBusy: PropTypes.bool,
    result: PropTypes.shape(Object),
    onCommitDryRun: PropTypes.func,
    th: PropTypes.number,
    emptyTransaction: PropTypes.bool,
    hasLatestCommitDryRunResult: PropTypes.bool,
};

const mapDispatchToProps = dispatch => ({
    onCommitDryRun(th) {
        dispatch(commitDryRun({ th }));
    },
});

const mapStateToProps = state => ({
    thIsBusy: selectThIsBusy(state),
    result: selectCommitDryRunResult(state),
    commitDryRunError: selectCommitDryRunError(state),
    th: selectReadWrite(state),
    hasLatestCommitDryRunResult: selectHasLatestCommitDryRunResult(state),
    emptyTransaction: transactionIsEmpty(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CommitDryRunTabContainer);

export {
    CommitDryRunTabContainer as _CommitDryRunTabContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
