import React from 'react';
import PropTypes from 'prop-types';

import styles from './LoadStyles';

const LoadFromPlainText = ({
    errorText,
    onBlur,
    onChange,
    value,
}) => (
    <div style={styles.container}>
        <div style={styles.plainContainer}>
            <div data-handle="transaction-data" style={styles.textAreaContainer}>
                <textarea
                    rows="5"
                    onBlur={onBlur}
                    onChange={onChange}
                    placeholder="Paste your transaction data here."
                    style={styles.textAreaStyle}
                    value={value}
                />
            </div>
        </div>
        {errorText && <div data-handle="load-from-text-error-message" style={styles.errorMessage}>
            { errorText }
        </div>}
    </div>
);

LoadFromPlainText.propTypes = {
    errorText: PropTypes.string,
    onBlur: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.string,
};
export default LoadFromPlainText;
