import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { translate, Messages } from 'modules/intl';
import style from './CommitButtonStyles';

const CommitButton = ({ commitInProgress, enableCommit, onCommit, shouldShowDone }) => (
    (<div style={style.wrapper}>
        <button
            disabled={!enableCommit}
            style={enableCommit ? defaultStyles.button : defaultStyles.buttonDisabled}
            onClick={onCommit}
            data-handle="commitButton"
        >
            <div style={style.label}>
                {(shouldShowDone || !commitInProgress) &&
                <div>{shouldShowDone ? translate(Messages.COMMITMANAGER.COMMIT_BUTTON_DONE) :
            translate(Messages.COMMITMANAGER.COMMIT_BUTTON_COMMIT)}</div>}
                {commitInProgress &&
                <div style={style.loader}><Loader color={defaultStyles.color3} /></div>}
            </div>
        </button>
    </div>)
);

CommitButton.propTypes = {
    commitInProgress: PropTypes.bool,
    shouldShowDone: PropTypes.bool,
    enableCommit: PropTypes.bool,
    onCommit: PropTypes.func.isRequired,
};

export default CommitButton;
