import {
    createNodeRowSelector,
    selectLoading,
    selectOffset,
    selectSortColumn,
    selectsortOrder,
    selectNode,
    selectRows,
} from 'modules/nodes/selectors';

import {
    selectTotalNumberOfResults,
} from 'modules/data/selectors';

import {
    PATH_QUEUE,
    QUEUE_ITEM_COMPLETED,
    QUEUE_ITEM_DEVICES,
    QUEUE_ITEM_DEVICE_LISTS,
    QUEUE_ITEM_FAILED,
    QUEUE_ITEM_TRANSIENT,
    QUEUE_ITEM_WAITING,
    getQueueItemQueryParams,
} from './path';

const nodeSelector = createNodeRowSelector();
export const selectCommitQueueRows = (state, ownProps) => (
    nodeSelector(state, { ...ownProps, path: PATH_QUEUE })
);
export const selectCommitQueueLoading = state => selectLoading(state, PATH_QUEUE);
export const selectCommitQueueOffset = state => selectOffset(state, PATH_QUEUE);
export const selectCommitQueueSortColumn = state => selectSortColumn(state, PATH_QUEUE);
export const selectCommitQueueSortOrder = state => selectsortOrder(state, PATH_QUEUE);
export const selectTotalNumberOfCommitQueues = state =>
    selectTotalNumberOfResults(state, PATH_QUEUE);

export const getIdFromProps = ownProps => ownProps.rowKey.replace(/[^0-9]/g, '');

export const selectQueueItemIsLoading = (state, ownProps) =>
    getQueueItemQueryParams(getIdFromProps(ownProps)).reduce(
        (acc, { path }) => {
            const node = selectNode(state, path);
            return acc || (node.isLoading !== undefined && node.isLoading);
        },
        false,
    );

const determineState = (deviceName, deviceLists) => {
    if (deviceLists[QUEUE_ITEM_TRANSIENT].find(e => deviceName === e[0])) return 'transient';
    if (deviceLists[QUEUE_ITEM_WAITING].find(e => deviceName === e[0])) return 'wait';
    if (deviceLists[QUEUE_ITEM_COMPLETED].find(e => deviceName === e[0])) return 'complete';
    if (deviceLists[QUEUE_ITEM_FAILED].find(e => deviceName === e.name)) return 'failed';
    return 'device';
};

export const selectQueueItemDevices = (state, ownProps) => {
    const queryParams = getQueueItemQueryParams(getIdFromProps(ownProps));
    const lists = queryParams.map(qp => selectRows(state, qp.path));
    const deviceLists = QUEUE_ITEM_DEVICE_LISTS.reduce((acc, k, i) => ({
        ...acc,
        [k]: lists[i],
    }), {});
    return deviceLists[QUEUE_ITEM_DEVICES].map((d) => {
        const deviceName = d[0];
        return ({
            device: deviceName,
            state: determineState(deviceName, deviceLists),
        });
    });
};

export {
    determineState as _determineState,
};
