import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { translate, Messages } from 'modules/intl';
import { outputToFile } from '@webui-one/core/utils/browser';
import { ensureConfigNode } from '@webui-one/core/utils/xml';

import { saveFile } from 'modules/transactions/actions';
import {
    selectSaveToFileError,
    transactionIsEmpty,
    selectValid,
} from 'modules/transactions/selectors';

import SaveToFile from './SaveToFile';

const mimeTypes = {
    json: 'application/json',
    xml: 'application/xml',
};

const getMimeType = format => (
    Object.keys(mimeTypes).includes(format) ? mimeTypes[format] : 'text/plain'
);

const validFileEndings = ['json', 'xml'];
const getFileEnding = format => (
    validFileEndings.includes(format) ? format : 'txt'
);

const getData = (timestamp, blobdata, fileending) => ({
    filename: `transaction-diff.${fileending}`,
    href: URL.createObjectURL(blobdata),
    timestamp,
});

const saveDataToFile = format => (res) => {
    const filedata = ensureConfigNode(res.result.local_node.data);
    const type = getMimeType(format);
    const fileending = getFileEnding(format);
    const blobdata = new Blob([filedata || ''], { type });
    const timestamp = Date.now();
    const data = getData(timestamp, blobdata, fileending);
    outputToFile(data);
};

class SaveToFileContainer extends React.Component {
    constructor(props) {
        super(props);
        this.doSave = this.doSave.bind(this);
        this.onGenerateFile = this.onGenerateFile.bind(this);
        this.files = [];

        this.state = {
            error: undefined,
            previewText: '',
            files: undefined,
            format: 'xml',
            saving: false,
        };
    }

    componentDidMount() {
        const { emptyTransaction, registerLoad, setLoadButtonEnabled } = this.props;
        registerLoad(this.doSave);
        setLoadButtonEnabled(!emptyTransaction);
    }

    componentDidUpdate(prevProps) {
        const { emptyTransaction, setLoadButtonEnabled } = this.props;
        if (emptyTransaction !== prevProps.emptyTransaction) {
            setLoadButtonEnabled(!emptyTransaction);
        }
    }

    onGenerateFile() {
        const { emptyTransaction, onSaveFile, validTrans } = this.props;
        const format = 'xml';

        if (emptyTransaction || !validTrans) {
            return Promise.resolve();
        }

        return this.setAsyncState({ saving: true })
            .then(() => onSaveFile(format))
            .then(saveDataToFile(format))
            .then(() => this.setAsyncState({ saving: false }));
    }

    setAsyncState(newState) {
        return new Promise(resolve => this.setState(newState, () => resolve()));
    }

    // eslint-disable-next-line class-methods-use-this
    doSave() {
        return Promise.resolve();
    }

    render() {
        const {
            emptyTransaction,
            saveToFileError,
            validTrans,
        } = this.props;
        const { loadFileError, previewText, data, saving } = this.state;
        const errorText = (saveToFileError || loadFileError)
            ? translate(Messages.COMMITMANAGER.LOAD_ERROR_MESSAGE)
            : null;
        const disableFileSelector = saving || !validTrans || !!emptyTransaction;
        return (
            <SaveToFile
                disableFileSelector={disableFileSelector}
                onGenerateFile={this.onGenerateFile}
                previewText={previewText}
                selectedFiles={data}
                errorText={errorText}
                saving={saving}
            />
        );
    }
}

const mapDispatchToProps = dispatch => ({
    onSaveFile(value, type) {
        return dispatch(saveFile(value, type));
    },
});

SaveToFileContainer.propTypes = {
    emptyTransaction: PropTypes.bool,
    onSaveFile: PropTypes.func.isRequired,
    registerLoad: PropTypes.func,
    saveToFileError: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.shape({}),
    ]),
    setLoadButtonEnabled: PropTypes.func,
    validTrans: PropTypes.bool,
};
SaveToFileContainer.defaultProps = {
    emptyTransaction: true,
};

const mapStateToProps = state => ({
    emptyTransaction: transactionIsEmpty(state),
    saveToFileError: selectSaveToFileError(state),
    validTrans: selectValid(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(SaveToFileContainer);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    SaveToFileContainer as _SaveToFileContainer,
};
