import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { isJson, isNonEmptyString, isXml } from '@webui-one/core/utils/string';
import { loadText } from 'modules/transactions/actions';
import { selectLoadFromTextError } from 'modules/transactions/selectors';
import { translate, Messages } from 'modules/intl';
import LoadFromPlainText from './LoadFromPlainText';

const validFormats = ['json', 'xml'];

const isValidFormat = format => validFormats.includes(format);
const isValidString = str => isNonEmptyString(str);
const shouldEnableLoadButton = (format, value) => (
    isValidFormat(format) && isValidString(value)
);

function guessFormat(value) {
    if (isJson(value)) {
        return 'json';
    }
    if (isXml(value)) {
        return 'xml';
    }
    return '';
}

const loadErrorMessage = translate(Messages.COMMITMANAGER.LOAD_ERROR_MESSAGE);
const formatErrorMessage = 'Text is not valid JSON or XML.';

class LoadFromPlainTextContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onBlur = this.onBlur.bind(this);
        this.onChange = this.onChange.bind(this);
        this.doLoad = this.doLoad.bind(this);
        this.state = {
            errorText: '',
            format: '',
            value: '',
        };
    }

    componentDidMount() {
        const { format, value } = this.state;
        const { registerLoad, setLoadButtonEnabled } = this.props;
        registerLoad(this.doLoad);
        setLoadButtonEnabled(shouldEnableLoadButton(format, value));
    }

    componentDidUpdate(prevProps) {
        const { loadFromTextError } = this.props;
        const { loadFromTextError: prevError } = prevProps;
        if (loadFromTextError && loadFromTextError !== prevError) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({
                errorText: loadErrorMessage,
            });
        }
    }

    onBlur(e) {
        const value = e.target.value;
        const format = guessFormat(value);
        const { setLoadButtonEnabled } = this.props;
        const errorText = format !== '' ? '' : formatErrorMessage;
        this.setState({ errorText, format, value });
        setLoadButtonEnabled(true);
        setLoadButtonEnabled(shouldEnableLoadButton(format, value));
    }

    onChange(e) {
        const value = e.target.value;
        this.setState({ value });
    }

    doLoad() {
        const { onLoadText } = this.props;
        const { format, value } = this.state;
        if (shouldEnableLoadButton(format, value)) {
            return onLoadText(value, format);
        }
        return Promise.resolve();
    }

    render() {
        const { errorText, format, value } = this.state;
        return (
            <LoadFromPlainText
                errorText={errorText}
                format={format}
                onBlur={this.onBlur}
                onChange={this.onChange}
                value={value}
            />
        );
    }
}

const mapDispatchToProps = dispatch => ({
    onLoadText(data, format) {
        return dispatch(loadText({ data, format }));
    },
});

LoadFromPlainTextContainer.propTypes = {
    loadFromTextError: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.shape({}),
    ]),
    onLoadText: PropTypes.func.isRequired,
    registerLoad: PropTypes.func.isRequired,
    setLoadButtonEnabled: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
    loadFromTextError: selectLoadFromTextError(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LoadFromPlainTextContainer);

export {
    shouldEnableLoadButton,
    formatErrorMessage,
    isValidFormat,
    isValidString,
    loadErrorMessage,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    LoadFromPlainTextContainer as _LoadFromPlainTextContainer,
};
