import React from 'react';
import DiffTable from '@webui-one/components/Diff/DiffTable';
import style from './CommitDryRunStyles';

const renderTableWithName = ({ name, data }, i) => ( // eslint-disable-line react/prop-types
    <div style={style.tableWrapper} key={`table-wrapper-${i}`}>
        <div style={style.tableName}>{name}</div>
        <DiffTable data={data} />
    </div>
);

const commitDryRunTab = (result) => {
    if (!(result && result.local_node && result.local_node.data)) {
        return <div style={style.message}>No configuration changes</div>;
    }

    if (result.lsa_node && result.lsa_node.length) {
        const localNode = { data: result.local_node.data, name: 'Local node' };
        const nodesData = [localNode, ...result.lsa_node.map(n => ({ ...n, name: `LSA node: ${n.name}` }))];
        return (
            <div style={style.tableWrapper}>
                { nodesData.map(renderTableWithName) }
            </div>
        );
    }
    return (<div style={style.tableWrapper}>
        <DiffTable data={result.local_node.data} />
    </div>);
};

export default commitDryRunTab;

