import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { getSchema } from 'modules/schemas/actions';
import {
    selectChangesWithDefaultValues,
} from 'modules/transactions/selectors';
import ChangesTab from './ChangesTab';

class ChangesTabContainer extends React.Component {
    constructor(props) {
        super(props);
        this.hasLoadedSchemaFor = {};
    }

    componentDidUpdate() {
        const {
            changes,
            onGetSchema,
        } = this.props;
        const defaultSetChanges = changes.filter(
            c => c.op === 'default_set' && c.value === undefined && this.hasLoadedSchemaFor[c.path] !== true,
        );

        defaultSetChanges.forEach((c) => {
            this.hasLoadedSchemaFor[c.path] = true;
            onGetSchema(c.path);
        });
    }

    render() {
        const { changes } = this.props;
        return <ChangesTab changes={changes} />;
    }
}

ChangesTabContainer.defaultProps = {
    changes: [],
};

ChangesTabContainer.propTypes = {
    changes: PropTypes.arrayOf(
        PropTypes.shape({
            device: PropTypes.string,
            text: PropTypes.string,
        }),
    ),
    onGetSchema: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
    onGetSchema(path) {
        dispatch(getSchema({ path, params: { levels: 1 } }));
    },
});

const mapStateToProps = state => ({
    changes: selectChangesWithDefaultValues(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ChangesTabContainer);

export {
    ChangesTabContainer as _ChangesTabContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
