import PropTypes from 'prop-types';
import React from 'react';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import style from './CommitDialogStyles';

const IgnoreWarnings = ({
    ignoreWarnings,
    onToggleIgnoreWarnings,
}) => (<div style={style.ignoreContainer}>
    <div style={style.ignoreDescription}>This transaction contains warnings.</div>
    <CheckBox
        name="ignore-commit-warnings"
        onSetValue={onToggleIgnoreWarnings}
        value={ignoreWarnings}
        >
        <span style={style.ignoreLabel}>Ignore warnings</span>
    </CheckBox>
</div>);


IgnoreWarnings.defaultProps = {
    ignoreWarnings: undefined,
    onToggleIgnoreWarnings: () => null,
};

IgnoreWarnings.propTypes = {
    ignoreWarnings: PropTypes.bool,
    onToggleIgnoreWarnings: PropTypes.func,
};


export default IgnoreWarnings;
