import List from '@webui-one/components/ConfigRender/List/List';
import Pagination from '@webui-one/components/ConfigRender/List/Pagination';
import PropTypes from 'prop-types';
import React from 'react';
import styles from '@webui-one/components/ConfigRender/List/ListStyles';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import { getCommitQueue } from 'modules/commitQueue/actions';
import { getLinkWithPath } from 'ConfigurationEditor';
import {
    createSortableHeaderFactory,
    createTextCellFactory,
    getDefaultColumn,
} from '@webui-one/components/ConfigRender/List/ListCells';
import {
    selectCommitQueueLoading,
    selectCommitQueueOffset,
    selectCommitQueueRows,
    selectCommitQueueSortColumn,
    selectCommitQueueSortOrder,
    selectTotalNumberOfCommitQueues,
} from 'modules/commitQueue/selectors';
import {
    ageToRelativeTimeString,
} from '@webui-one/core/utils/time';

import CommitQueueDevicesCellContainer from 'CommitManager/CommitQueueDevicesCellContainer';
import style from './CommitManagerStyles';

const STYLE_NAME = 'commitQueueList';
const CHUNK_SIZE = 10;
const listStyle = styles(STYLE_NAME);
const REFRESH_INTERVAL = 10000;
const createQueueLink = (text, key, rowKey) => (
    <Link to={getLinkWithPath(rowKey)} style={{ ...listStyle.list.row.link }}>
        {text}
    </Link>
);
const createCell = createTextCellFactory(STYLE_NAME);
const createAgeCell = (text, key, rowKey) =>
    createCell(ageToRelativeTimeString(text), key, rowKey);

const createDevicesCell = (text, key, rowKey) =>
    <CommitQueueDevicesCellContainer rowKey={rowKey} />;

class CommitQueueListContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onChangeSortOrder = this.onChangeSortOrder.bind(this);
        this.onGoToPage = this.onGoToPage.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
    }

    componentDidMount() {
        const { onLoad, sortColumn, sortOrder } = this.props;
        onLoad(CHUNK_SIZE, 1, sortColumn, sortOrder);
    }

    componentDidUpdate(prevProps) {
        const { isLoading: wasLoading } = prevProps;
        const { isLoading } = this.props;
        if (wasLoading && !isLoading) {
            this.resetRefresh();
        }
    }

    componentWillUnmount() {
        clearTimeout(this.refreshTimeout);
    }

    onChangeSortOrder(columnKey) {
        const {
            onLoad,
            sortOrder,
            sortColumn,
            isLoading,
        } = this.props;

        if (!isLoading) {
            const isDesc = columnKey === sortColumn && sortOrder !== 'descending';
            const isNone = columnKey === sortColumn && sortOrder === 'descending';
            let newSortorder = 'ascending';
            let newColumnKey = columnKey;
            if (isDesc) newSortorder = 'descending';
            if (isNone) {
                newSortorder = undefined;
                newColumnKey = undefined;
            }

            onLoad(CHUNK_SIZE, 1, newColumnKey, newSortorder);
        }
    }

    onGoToPage(offset) {
        const {
            isLoading,
            onLoad,
            sortOrder,
            sortColumn,
        } = this.props;

        if (!isLoading) {
            onLoad(CHUNK_SIZE, offset, sortColumn, sortOrder);
        }
    }

    onRefresh() {
        const { offset } = this.props;
        this.onGoToPage(offset);
    }

    resetRefresh() {
        clearTimeout(this.refreshTimeout);
        this.refreshTimeout = setTimeout(this.onRefresh, REFRESH_INTERVAL);
    }

    render() {
        const {
            rows,
            sortColumn,
            sortOrder: sortOrdering,
            isLoading,
            totalNumberOfResults,
            offset,
        } = this.props;

        if (!isLoading && (!rows || !rows.length)) {
            return <div style={style.message}>No commits in queue</div>;
        }

        const sortOrder = {
            columnKey: sortColumn,
            isDesc: sortOrdering === 'descending',
        };

        const createHeader = createSortableHeaderFactory(this.onChangeSortOrder, sortOrder);

        const columns = [{
            ...getDefaultColumn(),
            title: 'id',
            key: 'id',
            createHeader,
            createCell: createQueueLink,
        },
        {
            ...getDefaultColumn(),
            title: 'age',
            key: 'age',
            createHeader,
            createCell: createAgeCell,
        },
        {
            ...getDefaultColumn(),
            title: 'status',
            key: 'status',
            createHeader,
            createCell,
        },
        {
            ...getDefaultColumn(),
            title: 'atomic',
            key: 'is-atomic',
            createHeader,
            createCell,
        },
        {
            ...getDefaultColumn(),
            title: 'devices',
            key: 'devices',
            createHeader,
            createCell: createDevicesCell,
        }];

        const additionalElements = [
            <Pagination
                key="pagination"
                totalNumberOfResults={totalNumberOfResults}
                onGoToPage={this.onGoToPage}
                chunkSize={CHUNK_SIZE}
                offset={offset}
            />,
        ];

        return (<List
            columns={columns}
            rows={rows}
            styleName={STYLE_NAME}
            isLoading={isLoading}
            additionalElements={additionalElements}
            chunkSize={CHUNK_SIZE}
            LinkComponent={Link}
            offset={offset}
            shouldPadRows={false}
            totalNumberOfResults={totalNumberOfResults}
            shouldRetainColumnsSize={false}
            nrOfStaticColumns={0}
        />);
    }
}

CommitQueueListContainer.propTypes = {
    isLoading: PropTypes.bool,
    onLoad: PropTypes.func.isRequired,
    rows: PropTypes.arrayOf(Object),
    sortOrder: PropTypes.string,
    offset: PropTypes.number,
    sortColumn: PropTypes.string,
    totalNumberOfResults: PropTypes.number,
};

CommitQueueListContainer.defaultProps = {};


const mapDispatchToProps = dispatch => ({
    onLoad(chunkSize, offset, columnKey, sortOrder) {
        dispatch(getCommitQueue(chunkSize, offset, columnKey, sortOrder));
    },
});

const mapStateToProps = state => ({
    isLoading: selectCommitQueueLoading(state),
    offset: selectCommitQueueOffset(state),
    rows: selectCommitQueueRows(state, { keyNames: ['id'] }),
    sortColumn: selectCommitQueueSortColumn(state),
    sortOrder: selectCommitQueueSortOrder(state),
    totalNumberOfResults: selectTotalNumberOfCommitQueues(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CommitQueueListContainer);


export {
    createAgeCell as _createAgeCell,
    CommitQueueListContainer as _CommitQueueListContainer,
    createQueueLink as _createQueueLink,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
