import React from 'react';
import PropTypes from 'prop-types';
import DataContainer from 'Common/Components/ConfigRender/DataContainer';
import { getTruncatedPaths } from '@webui-one/core/keypath/kp-parser';
import { getLinkWithPath, CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Link from '@webui-one/components/Link';
import { getParentPath } from '@webui-one/core/utils/path';
import breadcrumbStyles from 'Common/Components/Breadcrumb/BreadcrumbStyles';
import {
    renderHeader,
    renderBody,
} from './Common';
import {
    isEmpty,
    isObject,
} from '@webui-one/core/utils/object';
import {
    isNonEmptyArray,
} from '@webui-one/core/utils/array';
import style, {
    warningStyles,
} from './CommitManagerStyles';

const renderErrorHeader = (index, numErrors, reason) =>
    <p style={warningStyles.errorHeader} data-handle="error-header">{`${index + 1}/${numErrors}. ${reason}`}</p>;

const renderError = (error, index, arr) => {
    if (!error.paths) {
        return (<div key={index}>
            {renderErrorHeader(index, arr.length, error.reason)}
        </div>);
    }
    const paths = arr.reduce((acc, e) => (e.paths ? [...acc, ...e.paths] : acc), []);
    const truncatedPaths = getTruncatedPaths(paths);
    let contextLink;
    if (error.schema && error.schema.data && error.schema.data.kind === 'leaf') {
        contextLink = (
            <Link
                style={breadcrumbStyles.link}
                to={getLinkWithPath(getParentPath(error.path))}
                >
                {`${getParentPath(error.path)}/`}
            </Link>
        );
    }
    return (<div style={warningStyles.page} key={index}>
        { error.paths && error.paths.map((path, ix) =>
            (<div key={ix} style={warningStyles.errorWrapper}>
                {renderErrorHeader(index, arr.length, `.../${truncatedPaths[path]} ${error.reason}`)}
                <div>
                    { contextLink }
                    <DataContainer
                        applicationName={CONFIGURATION_EDITOR_NAME}
                        hideIndicators
                        hideContainers
                        hideLists
                        includeSubfolders
                        showEmpty
                        showConfig
                        isEditMode
                        key={ix}
                        path={path}
                />
                </div>
            </div>),
        )}
    </div>);
};

const renderErrors = (error) => {
    if (!error.data) return false;
    if (!error.data.errors) return false;
    return error.data.errors.map(renderError);
};

const renderErrorMessage = (error) => {
    if (!error.message) return false;
    return <p style={warningStyles.messageContainer} data-handle="error" >{error.message}</p>;
};

const renderErrorDataMessage = (error) => {
    if (!error.data) return false;
    if (!error.data.message) return false;
    return (<p style={warningStyles.container} data-handle="errorMessage" >
        {`${error.data.message}`}
    </p>);
};

const renderConflicts = (conflicts) => {
    if (!isNonEmptyArray(conflicts)) return false;

    return <div>
        <table style={style.table}>
            {renderHeader()}
            {renderBody(conflicts)}
        </table>
    </div>;
};

const WarningsTab = ({
    error,
    conflicts,
    onRevalidateTrans,
    onResolveConflicts,
    disableRevalidateButton,
}) => {
    const disabled = !!disableRevalidateButton;
    if (!isObject(error) || isEmpty(error)) {
        return <div style={warningStyles.message}>No warnings</div>;
    }

    return (<div>
        {renderErrorMessage(error)}
        <div style={warningStyles.revalidateContainer}>
            {renderErrorDataMessage(error)}
            <button
                disabled={disabled}
                style={{ ...defaultStyles.button, ...style.reValidateButton }}
                onClick={onRevalidateTrans}
                data-handle="revalidate"
                    >
                        Re-validate
            </button>
            {
                isNonEmptyArray(conflicts) && (
                    <button
                        disabled={disabled}
                        style={defaultStyles.button}
                        onClick={onResolveConflicts}
                        data-handle="resolve-conflicts"
                        >
                                Resolve Transaction
                    </button>
                )
            }
        </div>
        <div style={style.scrollingContainer}>
            {renderErrors(error)}
            {renderConflicts(conflicts)}
        </div>
    </div>);
};

WarningsTab.defaultProps = {
    error: undefined,
};

WarningsTab.propTypes = {
    disableRevalidateButton: PropTypes.bool,
    conflicts: PropTypes.arrayOf(PropTypes.shape()),
    error: PropTypes.shape({
        type: PropTypes.string,
        message: PropTypes.string,
        errors: PropTypes.arrayOf(
            PropTypes.shape({
                reason: PropTypes.string,
                paths: PropTypes.arrayOf(PropTypes.string),
            }),
        ),
    }),
    onRevalidateTrans: PropTypes.func,
    onResolveConflicts: PropTypes.func,
};

export default WarningsTab;

export {
    WarningsTab as _WarningsTab,
    renderErrors as _renderErrors,
    renderErrorMessage as _renderErrorMessage,
    renderErrorDataMessage as _renderErrorDataMessage,
    renderError as _renderError,
};
