import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Loader from '@webui-one/components/Loading/Loader';
import {
    selectQueueItemDevices,
    selectQueueItemIsLoading,
    getIdFromProps,
} from 'modules/commitQueue/selectors';
import { getDevicesOfQueueItem } from 'modules/commitQueue/actions';

import style from './CommitQueueDevicesCellStyles';

export const CommitQueueDeviceCell = ({ device, state }) => (<div
    key={device}
    data-handle={`device-${device}-${state}`}
    style={style[state]}>
    {state === 'complete' && <span style={style.check}></span>}
    {device}
</div>
);
CommitQueueDeviceCell.propTypes = {
    device: PropTypes.string,
    state: PropTypes.string,
};

class CommitQueueDevicesCellContainer extends React.Component {
    componentDidMount() {
        const { isLoading, onLoad } = this.props;
        if (!isLoading) {
            onLoad();
        }
    }

    render() {
        const { devices, rowKey, isLoading } = this.props;
        if (isLoading) return <Loader color="#000" />;
        return (<div data-handle={`devices-${rowKey}`}>
            {devices.map(CommitQueueDeviceCell)}
        </div>);
    }
}

CommitQueueDevicesCellContainer.propTypes = {
    devices: PropTypes.arrayOf(Object).isRequired,
    isLoading: PropTypes.bool,
    onLoad: PropTypes.func,
    rowKey: PropTypes.string,
};

const mapStateToProps = (state, ownProps) => ({
    devices: selectQueueItemDevices(state, ownProps),
    isLoading: selectQueueItemIsLoading(state, ownProps),
});
const mapDispatchToProps = (dispatch, ownProps) => ({
    onLoad() {
        dispatch(getDevicesOfQueueItem(getIdFromProps(ownProps)));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CommitQueueDevicesCellContainer);

export {
    mapStateToProps as _mapStateToProps,
    mapDispatchToProps as _mapDispatchToProps,
    CommitQueueDevicesCellContainer as _CommitQueueDevicesCellContainer,
};
