import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { isFunction } from '@webui-one/core/utils/functional';
import { translate, Messages } from 'modules/intl';

import { loadFile } from 'modules/transactions/actions';
import { selectLoadFromFileError } from 'modules/transactions/selectors';

import LoadFromFile from './LoadFromFile';

// NOTE(dbildstr): only allow preview of 5 MiB big files for now
const MAX_PREVIEW_SIZE = 5 * 1024 * 1024;

const hasFiles = files => !!(Array.isArray(files) && files.length > 0);

const getExtensionFromFilename = (file) => {
    const match = /^(.*)\.(.*)$/.exec(file.name);
    const extension = match && match.length > 2 ? match[2] : 'txt';
    return extension;
};

const getSelectedFiles = files => (
    hasFiles(files) ? Array.prototype.map.call(files, f => f.name) : []
);

const browserSupportsFileAPI = () => isFunction(FileReader);

class LoadFromFileContainer extends React.Component {
    constructor(props) {
        super(props);
        this.doLoad = this.doLoad.bind(this);
        this.onChange = this.onChange.bind(this);
        this.files = [];

        this.state = {
            error: undefined,
            previewText: '',
            files: undefined,
            format: 'xml',
        };
    }

    componentDidMount() {
        const { registerLoad, setLoadButtonEnabled } = this.props;
        registerLoad(this.doLoad);
        setLoadButtonEnabled(false);
    }

    onChange(evt) {
        this.fi = evt.target;
        this.files = Array.prototype.slice.call(this.fi.files);
        this.setState({ files: getSelectedFiles(this.files) });
        this.props.setLoadButtonEnabled(hasFiles(this.files));
        this.updatePreview(this.files);
    }

    updatePreview(files) {
        if (!hasFiles(files)) {
            this.setState({
                previewText: translate(
                    Messages.COMMITMANAGER.PREVIEW_NO_ISSUES,
                ),
            });
            return;
        }
        const theFile = files[0];

        if (theFile.size > MAX_PREVIEW_SIZE) {
            this.setState({
                previewText: translate(
                    Messages.COMMITMANAGER.PREVIEW_FILE_TO_BIG,
                ),
            });
            return;
        }

        if (!browserSupportsFileAPI()) {
            this.setState({
                previewText: translate(
                    Messages.COMMITMANAGER.PREVIEW_BROWSER_NOT_SUPPORTED,
                ),
            });
            return;
        }

        const reader = new FileReader();
        reader.onload = () => {
            this.setState({ previewText: reader.result });
        };
        reader.onerror = () => {
            this.setState({
                previewText: translate(
                    Messages.COMMITMANAGER.PREVIEW_FAILED_TO_READ_FILE,
                ),
            });
        };
        reader.readAsText(theFile);
    }

    doLoad() {
        const { onLoadFile } = this.props;
        this.setState({ loadFileError: null });
        if (hasFiles(this.files)) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                const theFile = this.files[0];
                const extension = getExtensionFromFilename(theFile);
                reader.onload = () => {
                    resolve({ result: reader.result, extension });
                };
                reader.onerror = error => reject(new Error(error));
                reader.readAsBinaryString(theFile);
            }).then(res => (
                onLoadFile(res.result, res.extension)
            )).catch((err) => {
                this.setState({ loadFileError: err });
                return err;
            });
        }
        return Promise.resolve();
    }

    render() {
        const { loadFromFileError } = this.props;
        const { loadFileError, files, previewText } = this.state;
        const errorText = (loadFromFileError || loadFileError)
            ? translate(Messages.COMMITMANAGER.LOAD_ERROR_MESSAGE)
            : null;
        return (
            <LoadFromFile
                onChange={this.onChange}
                previewText={previewText}
                selectedFiles={files}
                errorText={errorText}
            />
        );
    }
}

const mapDispatchToProps = dispatch => ({
    onLoadFile(data, format) {
        return dispatch(loadFile({ data, format }));
    },
});

LoadFromFileContainer.propTypes = {
    loadFromFileError: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.shape({}),
    ]),
    onLoadFile: PropTypes.func.isRequired,
    registerLoad: PropTypes.func,
    setLoadButtonEnabled: PropTypes.func,
};

const mapStateToProps = state => ({
    loadFromFileError: selectLoadFromFileError(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(LoadFromFileContainer);

export {
    MAX_PREVIEW_SIZE,
    hasFiles as _hasFiles,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    LoadFromFileContainer as _LoadFromFileContainer,
};
