export const QUERY_SELECTION = ['id', 'age', 'status', 'is-atomic'];
export const PATH = '/ncs:devices/commit-queue';
export const PATH_QUEUE = `${PATH}/queue-item`;


export const QUEUE_ITEM_DEVICES = 'devices';
export const QUEUE_ITEM_WAITING = 'waiting-for';
export const QUEUE_ITEM_TRANSIENT = 'transient-errors';
export const QUEUE_ITEM_COMPLETED = 'completed';
export const QUEUE_ITEM_FAILED = 'failed';

export const QUEUE_ITEM_DEVICE_LISTS = [
    QUEUE_ITEM_DEVICES,
    QUEUE_ITEM_WAITING,
    QUEUE_ITEM_TRANSIENT,
    QUEUE_ITEM_COMPLETED,
    QUEUE_ITEM_FAILED,
];

export const QUEUE_ITEM_DEVICE_LISTS_SELECTION = {
    [QUEUE_ITEM_FAILED]: ['name'],
};

export const itemIdToPath = queueItemId => leaf => ({
    path: `/ncs:devices/commit-queue/queue-item{${queueItemId}}/${leaf}`,
    selection: QUEUE_ITEM_DEVICE_LISTS_SELECTION[leaf],
});

export const getQueueItemQueryParams = queueItemId =>
    QUEUE_ITEM_DEVICE_LISTS.map(itemIdToPath(queueItemId));
