import React from 'react';
import { connect } from 'react-redux';
import { validateAndCommit } from 'modules/transactions/actions';
import PropTypes from 'prop-types';
import {
    selectCommitInProgress,
    selectCommitOptions,
    selectReadWrite,
    selectValid,
    selectThIsBusy,
    selectWarnings,
    transactionIsEmpty,
} from 'modules/transactions/selectors';
import CommitButton from './CommitButton';
import CommitDialog from './CommitDialog';
import { CommitOptionsType } from './CommitOptions';

const DONE_MESSAGE_TIMEOUT = 5000;

const commitIsDone = (prevProps, props) => (
    prevProps.commitInProgress === true && !props.commitInProgress
);

class CommitButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.toggleIgnoreWarnings = this.toggleIgnoreWarnings.bind(this);
        this.labelTimeout = null;
        this.state = {
            shouldIgnoreWarnings: false,
            shouldShowDone: false,
            shouldShowDialog: false,
        };
    }

    componentDidUpdate(prevProps) {
        if (commitIsDone(prevProps, this.props)) {
            this.showDoneLabel(DONE_MESSAGE_TIMEOUT);
            this.hideDialog();
        }
    }

    componentWillUnmount() {
        clearTimeout(this.labelTimeout);
        this.labelTimeout = null;
    }

    showDoneLabel(msecs) {
        this.setState({ shouldShowDone: true });
        this.labelTimeout = setTimeout(() => {
            this.hideDoneLabel();
        }, msecs);
    }

    hideDoneLabel() {
        this.setState({ shouldShowDone: false });
    }

    showDialog() {
        this.setState({ shouldShowDialog: true });
    }

    hideDialog() {
        this.setState({ shouldShowDialog: false });
    }

    parseCommitFlags() {
        const { commitOptions } = this.props;
        let activeOptions = Object.keys(commitOptions).filter(
            key => !!commitOptions[key]);
        if (!commitOptions['commit-queue']) {
            activeOptions = activeOptions.filter(
                option => !option.startsWith('commit-queue'),
            );
        }
        return activeOptions.map((key) => {
            const value = commitOptions[key];
            return typeof value === 'string' ? `${key}=${value}` : key;
        });
    }

    toggleIgnoreWarnings() {
        const { shouldIgnoreWarnings } = this.state;
        this.setState({ shouldIgnoreWarnings: !shouldIgnoreWarnings });
    }

    render() {
        const {
            commitInProgress,
            emptyTransaction,
            thIsBusy,
            th,
            valid,
            onCommit,
            warnings,
        } = this.props;
        const {
            shouldIgnoreWarnings,
            shouldShowDialog,
        } = this.state;
        const enableCommit = Number.isFinite(th)
            && valid
            && !emptyTransaction
            && !thIsBusy
            && !commitInProgress;
        const { shouldShowDone } = this.state;

        const hasWarnings = Array.isArray(warnings) && warnings.length > 0;
        const isOkToCommit = !commitInProgress && (!hasWarnings || shouldIgnoreWarnings);

        return (
            <div>
                <CommitButton
                    commitInProgress={commitInProgress}
                    shouldShowDone={shouldShowDone}
                    enableCommit={enableCommit}
                    onCommit={() => this.showDialog()}
                 />
                {shouldShowDialog && <CommitDialog
                    hasWarnings={hasWarnings}
                    ignoreWarnings={shouldIgnoreWarnings}
                    onToggleIgnoreWarnings={this.toggleIgnoreWarnings}
                    isOkToCommit={isOkToCommit}
                    onCommit={() => {
                        this.hideDialog();
                        onCommit(this.parseCommitFlags());
                    }}
                    onCancel={() => this.hideDialog()}
                />}
            </div>
        );
    }
}

CommitButtonContainer.defaultProps = {
    commitInProgress: undefined,
    commitOptions: {},
    emptyTransaction: undefined,
    thIsBusy: undefined,
    extraStyles: {},
    th: undefined,
    valid: undefined,
};

CommitButtonContainer.propTypes = {
    commitInProgress: PropTypes.bool,
    commitOptions: CommitOptionsType,
    emptyTransaction: PropTypes.bool,
    thIsBusy: PropTypes.bool,
    onCommit: PropTypes.func,
    th: PropTypes.number,
    valid: PropTypes.bool,
    warnings: PropTypes.arrayOf(PropTypes.shape()),
};

const mapStateToProps = state => ({
    commitInProgress: selectCommitInProgress(state),
    commitOptions: selectCommitOptions(state),
    emptyTransaction: transactionIsEmpty(state),
    thIsBusy: selectThIsBusy(state),
    th: selectReadWrite(state),
    valid: selectValid(state),
    warnings: selectWarnings(state),
});

const mapDispatchToProps = dispatch => ({
    onCommit(flags) {
        dispatch(validateAndCommit({ flags }));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(CommitButtonContainer);

export {
    CommitButtonContainer as _CommitButtonContainer,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
    DONE_MESSAGE_TIMEOUT as _DONE_MESSAGE_TIMEOUT,
};
