import React from 'react';
import PropTypes from 'prop-types';
import { isEmptyArray } from '@webui-one/core/utils/array';
import style from './CommitManagerStyles';

const NativeCommitDryRunTab = ({ devices }) => {
    if (isEmptyArray(devices)) {
        return <div style={style.message}>No devices affected</div>;
    }

    return (
        <div>
            <div style={style.nativeCommitPane}>
                {devices.map(({ name, data }) => (<div data-handle={`native-${name}`} key={name} >
                    <div style={style.nativeDevice} data-handle="native-name">{name}</div>
                    <div style={style.nativeCommitDiff} data-handle="native-diff">{data}</div>
                </div>))}
            </div>
        </div>
    );
};

NativeCommitDryRunTab.defaultProps = {
    devices: [],
};

NativeCommitDryRunTab.propTypes = {
    devices: PropTypes.arrayOf(PropTypes.shape({
        data: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
    })),
};

export default NativeCommitDryRunTab;
