import React from 'react';
import { connect } from 'react-redux';
import { isFunction } from '@webui-one/core/utils/functional';
import PropTypes from 'prop-types';

import {
    selectLoadInProgress,
} from 'modules/transactions/selectors';
import { Messages, translate } from 'modules/intl';

import LoadButton from './LoadButton';
import LoadFromPlainTextContainer from './LoadFromPlainTextContainer';
import LoadFromFileContainer from './LoadFromFileContainer';
import SaveToFileContainer from './SaveToFileContainer';
import RollbackContainer from './RollbackContainer';

const buildRollbacks = (registerLoad, setLoadButtonEnabled) => (
    <RollbackContainer
        registerLoad={registerLoad}
        setLoadButtonEnabled={setLoadButtonEnabled}
    />
);

const buildFiles = (registerLoad, setLoadButtonEnabled) => (
    <LoadFromFileContainer
        registerLoad={registerLoad}
        setLoadButtonEnabled={setLoadButtonEnabled}
    />
);


const buildText = (registerLoad, setLoadButtonEnabled) => (
    <LoadFromPlainTextContainer
        registerLoad={registerLoad}
        setLoadButtonEnabled={setLoadButtonEnabled}
    />
);

const buildSave = (registerLoad, setLoadButtonEnabled) => (
    <SaveToFileContainer
        registerLoad={registerLoad}
        setLoadButtonEnabled={setLoadButtonEnabled}
    />
);

class LoadButtonContainer extends React.Component {
    constructor(props) {
        super(props);
        this.onChangeTab = this.onChangeTab.bind(this);
        this.onLoad = this.onLoad.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.openModal = this.openModal.bind(this);
        this.registerLoad = this.registerLoad.bind(this);
        this.setLoadButtonEnabled = this.setLoadButtonEnabled.bind(this);

        this.state = {
            activePageIndex: 0,
            doLoad: null,
            enableLoadButton: true,
            showModal: false,
        };
    }

    onLoad() {
        const { doLoad } = this.state;
        if (isFunction(doLoad)) {
            const prom = doLoad();
            if (prom.then) {
                return prom.then((res) => {
                    if (!res || !res.error) {
                        this.closeModal();
                    }
                });
            }
            this.closeModal();
        }
        return Promise.resolve();
    }

    onChangeTab(activePageIndex) {
        this.setState({ activePageIndex });
    }

    setLoadButtonEnabled(enableLoadButton) {
        this.setState({ enableLoadButton });
    }

    closeModal() {
        this.setState({ showModal: false });
    }

    openModal() {
        this.setState({ showModal: true });
    }

    registerLoad(doLoad) {
        this.setState({ doLoad });
    }

    buildPages() {
        if (!this.pages) {
            this.pages = [
                {
                    name: 'rollbacks',
                    page: buildRollbacks(this.registerLoad, this.setLoadButtonEnabled),
                    tab: 'Rollbacks',
                },
                {
                    name: 'files',
                    page: buildFiles(
                        this.registerLoad,
                        this.setLoadButtonEnabled,
                    ),
                    tab: 'Files',
                },
                {
                    name: 'paste',
                    page: buildText(
                        this.registerLoad,
                        this.setLoadButtonEnabled,
                    ),
                    tab: 'Paste',
                },
                {
                    confirmButtonTitle: translate(Messages.MODAL.OK_BUTTON_TITLE),
                    name: 'save',
                    page: buildSave(
                        this.registerLoad,
                        this.setLoadButtonEnabled,
                    ),
                    tab: 'Save',
                },
            ];
        }
        return this.pages;
    }

    render() {
        const {
            loadInProgress,
        } = this.props;
        const {
            activePageIndex,
            enableLoadButton,
            showModal,
        } = this.state;
        const pages = this.buildPages();
        const enable = !!enableLoadButton && !loadInProgress;
        return (
            <LoadButton
                activePageIndex={activePageIndex}
                closeModal={this.closeModal}
                enableModal
                enableLoadButton={enable}
                loadInProgress={loadInProgress}
                onChangeTab={this.onChangeTab}
                onLoad={this.onLoad}
                openModal={this.openModal}
                pages={pages}
                showModal={showModal}
            />
        );
    }
}

LoadButtonContainer.propTypes = {
    loadInProgress: PropTypes.bool,
};

const mapStateToProps = state => ({
    loadInProgress: selectLoadInProgress(state),
});

export default connect(
    mapStateToProps,
    undefined,
)(LoadButtonContainer);

export {
    mapStateToProps as _mapStateToProps,
    LoadButtonContainer as _LoadButtonContainer,
};
