import { getSchema } from 'modules/schemas/actions';
import {
    query,
} from 'modules/nodes/actions';

import {
    PATH,
    PATH_QUEUE,
    QUERY_SELECTION,
    getQueueItemQueryParams,
} from './path';

// eslint-disable-next-line import/prefer-default-export
export const getCommitQueue = (chunkSize, offset, columnKey, sortorder, xpathExpression) =>
    dispatch => dispatch(getSchema({
        path: PATH,
        params: {
            levels: -1,
            stop_on_list: true,
        },
        reload: true,
    })).then(() => dispatch(query({
        selection: QUERY_SELECTION,
        path: PATH_QUEUE,
        sort: columnKey,
        sortOrder: sortorder,
        initialOffset: offset,
        chunkSize,
        result_as: 'string',
        xpathExpr: xpathExpression,
        contextNode: PATH,
    })));

export const getDevicesOfQueueItem = queueItemId =>
    dispatch => Promise.all(getQueueItemQueryParams(queueItemId).map(({ path, selection }) =>
        dispatch(query({ selection, path })),
    ));
