import React from 'react';
import PropTypes from 'prop-types';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import Loader from '@webui-one/components/Loading/Loader';
import TabDeck from '@webui-one/components/TabDeck/TabDeck';
import { isString } from '@webui-one/core/utils/string';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { Messages, translate } from 'modules/intl';
import style from './LoadStyles';

const getConfirmButtonTitle = (pages, activePageIndex) => {
    const customTitle = Array.isArray(pages) &&
        pages[activePageIndex] &&
        pages[activePageIndex].confirmButtonTitle;
    return isString(customTitle)
        ? customTitle
        : translate(Messages.MODAL.LOAD_BUTTON_TITLE);
};

const LoadButton = ({
    activePageIndex,
    closeModal,
    enableModal,
    enableLoadButton,
    loadInProgress,
    onChangeTab,
    onLoad,
    openModal,
    showModal,
    pages,
}) => {
    const confirmTitle = loadInProgress
        ? (<Loader color={defaultStyles.color3} extraStyle={style.loadingDots} />)
        : getConfirmButtonTitle(pages, activePageIndex);
    return (
        <div style={style.loadContainerStyle}>
            <ModalConfirm
                confirmTitle={confirmTitle}
                title={translate(Messages.COMMITMANAGER.ROLLBACKBUTTON_DIALOG_TITLE)}
                shouldRender={showModal}
                onConfirm={onLoad}
                formIsValid={!!enableLoadButton}
                onCancel={closeModal}
            >
                <TabDeck
                    extraStyle={style.tabDeck}
                    onChangeTab={onChangeTab}
                    tabs={pages}
                    defaultIndex={activePageIndex}
                />
            </ModalConfirm>
            <button
                data-handle="show-load-save-modal-button"
                disabled={!enableModal}
                style={defaultStyles.button2}
                onClick={openModal}
            >Load/Save</button>
        </div>
    );
};

LoadButton.propTypes = {
    activePageIndex: PropTypes.number,
    closeModal: PropTypes.func,
    enableModal: PropTypes.bool,
    enableLoadButton: PropTypes.bool,
    loadInProgress: PropTypes.bool,
    onChangeTab: PropTypes.func,
    onLoad: PropTypes.func,
    openModal: PropTypes.func,
    pages: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.string,
            page: PropTypes.element,
        }),
    ),
    showModal: PropTypes.bool,
};

LoadButton.defaultProps = {
    activePageIndex: 0,
};

export default LoadButton;

export {
    getConfirmButtonTitle as _getConfirmButtonTitle,
};
