import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Loader from '@webui-one/components/Loading/Loader';
import { translate, Messages } from 'modules/intl';
import styles from './LoadStyles';
import style from './CommitButtonStyles';

const SaveToFile = ({
    disableFileSelector,
    errorText,
    onGenerateFile,
    saving,
}) => {
    const buttonStyle = !disableFileSelector
        ? styles.generateFileButton
        : styles.generateFileButtonDisabled;

    const btnTitle = saving
        ? (<div style={style.loader}><Loader color={defaultStyles.color3} /></div>)
        : translate(Messages.COMMITMANAGER.GENERATE_FILE_BUTTON_TITLE);

    return (
        <div style={styles.container}>
            <div style={styles.fileUpload}>
                <button
                    data-handle="save-to-file-generator"
                    onClick={onGenerateFile}
                    style={buttonStyle}
                    disabled={!!disableFileSelector}
                >{btnTitle}</button>
            </div>
            {errorText && <div
                data-handle="save-to-file-error-text"
                style={styles.errorMessage}
            >
                { errorText }
            </div>}
        </div>
    );
};

export default SaveToFile;
SaveToFile.propTypes = {
    disableFileSelector: PropTypes.bool,
    errorText: PropTypes.string,
    onGenerateFile: PropTypes.func,
    saving: PropTypes.bool,
};

SaveToFile.defaultProps = {
    disableFileSelector: true,
    saving: false,
};
