import React from 'react';
import PropTypes from 'prop-types';
import { isEmptyArray } from '@webui-one/core/utils/array';
import style from './CommitManagerStyles';
import {
    renderHeader,
    renderBody,
} from './Common';

const ChangesTab = ({ changes = [] }) => {
    if (isEmptyArray(changes)) {
        return <div style={style.message}>No changes</div>;
    }

    return (
        <table style={style.table}>
            {renderHeader()}
            {renderBody(changes)}
        </table>
    );
};

ChangesTab.defaultProps = {
    changes: [],
};

ChangesTab.propTypes = {
    changes: PropTypes.arrayOf(
        PropTypes.shape({
            path: PropTypes.string,
            op: PropTypes.string,
            value: PropTypes.string,
            old: PropTypes.string,
        }),
    ),
};

export default ChangesTab;
