import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { translate, Messages } from 'modules/intl';
import styles from './LoadStyles';

const renderFiles = (selectedFiles) => {
    if (!Array.isArray(selectedFiles) || selectedFiles.length < 1) {
        return null;
    }
    return (
        <div data-handle="selected-files" style={styles.selectedFilesStyle}>
            {selectedFiles.map((filename, index) => (
                <div key={`${index}`}>{filename}</div>
            ))}
        </div>
    );
};

class LoadFromFile extends React.Component {
    constructor(props) {
        super(props);
        this.onClick = this.onClick.bind(this);
    }

    onClick() {
        this.fi.click();
    }

    render() {
        const { errorText, selectedFiles, onChange, previewText } = this.props;
        return (
            <div style={styles.container}>
                <div style={styles.fileUpload}>
                    <button
                        data-handle="load-from-file-selector"
                        onClick={this.onClick}
                        style={defaultStyles.button}
                    >{translate(Messages.COMMITMANAGER.CHOOSE_FILE_BUTTON_TITLE)}</button>
                    <input
                        style={styles.fileSelector}
                        onChange={onChange}
                        ref={(r) => { this.fi = r; }}
                        type="file"
                    />
                </div>
                {renderFiles(selectedFiles)}
                <div style={styles.previewTextAreaContainer}>
                    <textarea
                        disabled
                        rows="5"
                        onChange={onChange}
                        style={styles.textAreaStyle}
                        value={previewText}
                    />
                </div>
                {errorText && <div
                    data-handle="load-from-file-error-text"
                    style={styles.errorMessage}
                >
                    { errorText }
                </div>}
            </div>
        );
    }
}

export default LoadFromFile;
LoadFromFile.propTypes = {
    errorText: PropTypes.string,
    onChange: PropTypes.func,
    previewText: PropTypes.string,
    selectedFiles: PropTypes.arrayOf(PropTypes.string),
};
