import React from 'react';
import PropTypes from 'prop-types';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import modalStyles from '@webui-one/components/Modal/ModalStyles';
import { Messages, translate } from 'modules/intl';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './RevertButtonStyles';

class RevertButton extends React.Component {
    constructor(props) {
        super(props);
        this.openModal = this.openModal.bind(this);
        this.onConfirm = this.onConfirm.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.state = {
            showModal: false,
        };
    }
    onConfirm() {
        const { th, onRevert } = this.props;
        onRevert(th);
    }

    closeModal() {
        this.setState({ showModal: false });
    }

    openModal() {
        this.setState({ showModal: true });
    }

    render() {
        const { enableRevert } = this.props;
        return (
            <div style={style.container}>
                <ModalConfirm
                    shouldRender={this.state.showModal}
                    onConfirm={this.onConfirm}
                    formIsValid
                    onRequestClose={this.closeModal}
                >
                    <div style={modalStyles.header}>
                        {translate(Messages.COMMITMANAGER.REVERTBUTTON_CONFIRM_TITLE)}
                    </div>
                    <div style={modalStyles.bread}>
                        {translate(Messages.COMMITMANAGER.REVERTBUTTON_CONFIRM_BODY)}
                    </div>
                </ModalConfirm>
                <button
                    disabled={!enableRevert}
                    style={enableRevert ? defaultStyles.button2 : defaultStyles.button2Disabled}
                    onClick={this.openModal}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                    data-handle="revert"
                     >Revert</button>
            </div>
        );
    }
}

RevertButton.propTypes = {
    enableRevert: PropTypes.bool,
    th: PropTypes.number,
    onRevert: PropTypes.func,
};

export default RevertButton;
