import PropTypes from 'prop-types';
import React from 'react';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import Input from '@webui-one/components/ConfigRender/Input/Input';
import Select from '@webui-one/components/ConfigRender/Select/Select';
import EditSymbol from '@webui-one/styles/static/EditSymbol';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './CommitDialogStyles';

class CommitOptions extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isConfigMode: false };
    }

    render() {
        const { commitOptions, onSetCommmitOption } = this.props;
        const { isConfigMode } = this.state;
        const {
            'commit-queue-atomic': atomic,
            'commit-queue-block-others': blockOthers,
            'commit-queue': commitQueue,
            'commit-queue-error-option': errorOption,
            'commit-queue-lock': lock,
            'no-lsa': noLsa,
            'no-networking': noNetworking,
            'no-out-of-sync-check': noOutOfSyncCheck,
            'no-overwrite': noOverwrite,
            'no-revision-drop': noRevisionDrop,
            'no-deploy': noDeploy,
            'commit-queue-tag': tag,
            'commit-queue-timeout': timeout,
            'use-lsa': useLsa,
        } = commitOptions;
        const hasOption = noRevisionDrop || noDeploy || (commitQueue && commitQueue !== 'bypass') ||
                noNetworking || noOutOfSyncCheck || noOverwrite || useLsa || noLsa;

        return (<div style={style.frame}>
            <div style={style.topRow}>
                <div style={style.name}>{(isConfigMode || hasOption) ?
                    'Commit options:' : 'No commit options set'}</div>
                <button
                    data-handle="penButton"
                    onClick={() => this.setState({ isConfigMode: !isConfigMode })}
                    style={style.editButton}
            ><EditSymbol color={defaultStyles.color1} active={isConfigMode} /></button>
            </div>
            {(isConfigMode || hasOption) && <div style={style.optionsBox}>
                {(isConfigMode || noRevisionDrop) &&
                <div data-handle="noRevisionDrop" style={style.row}>
                    <div style={style.name}>No revision drop</div>
                    {isConfigMode && <CheckBox
                        value={!!noRevisionDrop}
                        onSetValue={value => onSetCommmitOption('no-revision-drop', value)}
                />}
                </div>}
                {(isConfigMode || noDeploy) &&
                <div data-handle="noDeploy" style={style.row}>
                    <div style={style.name}>No deploy</div>
                    {isConfigMode && <CheckBox
                        value={!!noDeploy}
                        onSetValue={value => onSetCommmitOption('no-deploy', value)}
                />}
                </div>}
                {(isConfigMode || (commitQueue && (commitQueue !== 'bypass'))) &&
                <div data-handle="commitQueue" style={style.row}>
                    <div style={style.name}>{`Commit queue${isConfigMode ? '' : `: ${commitQueue}`}`}</div>
                    {isConfigMode && <Select
                        options={['', 'async', 'sync', 'bypass']}
                        onSetValue={value => onSetCommmitOption('commit-queue', value)}
                        value={commitQueue}
                 />}
                </div>}
                {(commitQueue && commitQueue !== 'bypass') && <div style={style.queueOptionsWrapper}>
                    {(isConfigMode || atomic) &&
                    <div data-handle="atomic" style={style.row}>
                        <div style={style.name}>Atomic</div>
                        {isConfigMode && <Select
                            options={['', 'true', 'false']}
                            onSetValue={value => onSetCommmitOption('commit-queue-atomic', value)}
                            value={atomic}
                    />}
                    </div>}
                    {(isConfigMode || blockOthers) &&
                    <div data-handle="blockOthers" style={style.row}>
                        <div style={style.name}>Block others</div>
                        {isConfigMode && <CheckBox
                            value={!!blockOthers}
                            onSetValue={value => onSetCommmitOption(
                                'commit-queue-block-others', value,
                                )}
                    />}
                    </div>}
                    {(isConfigMode || lock) &&
                    <div data-handle="lock" style={style.row}>
                        <div style={style.name}>Lock</div>
                        {isConfigMode && <CheckBox
                            value={!!lock}
                            onSetValue={value => onSetCommmitOption('commit-queue-lock', value)}
                    />}
                    </div>}
                    {(isConfigMode || tag) &&
                    <div data-handle="tag" style={style.row} onKeyDown={e => e.stopPropagation()} role="none">
                        <div style={style.name}>{`Tag${isConfigMode ? '' : `: ${tag}`}`}</div>
                        <div style={style.inputWrapper}>
                            {isConfigMode && <Input
                                editable
                                onSetValue={value => onSetCommmitOption('commit-queue-tag', value)}
                                value={tag}
                 />}
                        </div>
                    </div>}
                    {(isConfigMode || timeout) &&
                    <div data-handle="timeout" style={style.row} onKeyDown={e => e.stopPropagation()} role="none">
                        <div style={style.name}>{`Timeout${isConfigMode ? '' : `: ${timeout}`}`}</div>
                        <div style={style.inputWrapper}>
                            {isConfigMode && <Input
                                editable
                                onSetValue={value => onSetCommmitOption('commit-queue-timeout', value)}
                                value={timeout}
                 />}
                        </div>
                    </div>}
                    {(isConfigMode || (errorOption)) &&
                    <div data-handle="errorOption" style={style.row}>
                        <div style={style.name}>{`Error option${isConfigMode ? '' : `: ${errorOption}`}`}</div>
                        {isConfigMode && <Select
                            options={['', 'continue-on-error', 'rollback-on-error', 'bypass-on-error']}
                            optionsInfo={['', 'continue', 'rollback', 'bypass']}
                            formatOption={(val, info) => info}
                            onSetValue={value => onSetCommmitOption('commit-queue-error-option', value)}
                            value={errorOption}
                 />}
                    </div>}
                </div>}
                {(isConfigMode || noNetworking) &&
                <div data-handle="noNetworking" style={style.row}>
                    <div style={style.name}>No networking</div>
                    {isConfigMode && <CheckBox
                        value={!!noNetworking}
                        onSetValue={value => onSetCommmitOption('no-networking', value)}
                />}
                </div>}
                {(isConfigMode || noOutOfSyncCheck) &&
                <div data-handle="noOutOfSyncCheck" style={style.row}>
                    <div style={style.name}>No out of sync check</div>
                    {isConfigMode && <CheckBox
                        value={!!noOutOfSyncCheck}
                        onSetValue={value => onSetCommmitOption('no-out-of-sync-check', value)}
                />}
                </div>}
                {(isConfigMode || noOverwrite) &&
                <div data-handle="noOverwrite" style={style.row}>
                    <div style={style.name}>No overwrite</div>
                    {isConfigMode && <CheckBox
                        value={!!noOverwrite}
                        onSetValue={value => onSetCommmitOption('no-overwrite', value)}
                />}
                </div>}
                {(isConfigMode || useLsa) &&
                <div data-handle="useLsa" style={style.row}>
                    <div style={style.name}>Use LSA</div>
                    {isConfigMode && <CheckBox
                        value={!!useLsa}
                        onSetValue={value => onSetCommmitOption('use-lsa', value)}
                />}
                </div>}
                {(isConfigMode || noLsa) &&
                <div data-handle="noLsa" style={style.row}>
                    <div style={style.name}>No LSA</div>
                    {isConfigMode && <CheckBox
                        value={!!noLsa}
                        onSetValue={value => onSetCommmitOption('no-lsa', value)}
                />}
                </div>}
            </div>}
        </div>);
    }
}


CommitOptions.defaultProps = {
    commitOptions: {},
    onSetCommmitOption: () => null,
};

export const CommitOptionsType = PropTypes.shape({
    'commit-queue-atomic': PropTypes.boolean,
    'commit-queue-block-others': PropTypes.boolean,
    'commit-queue': PropTypes.boolean,
    'commit-queue-error-option': PropTypes.string,
    'commit-queue-lock': PropTypes.boolean,
    'commit-queue-tag': PropTypes.string,
    'commit-queue-timeout': PropTypes.string,
    'no-deploy': PropTypes.boolean,
    'no-lsa': PropTypes.boolean,
    'no-networking': PropTypes.boolean,
    'no-out-of-sync-check': PropTypes.boolean,
    'no-overwrite': PropTypes.boolean,
    'no-revision-drop': PropTypes.boolean,
    'use-lsa': PropTypes.boolean,
});

CommitOptions.propTypes = {
    commitOptions: CommitOptionsType,
    onSetCommmitOption: PropTypes.func,
};


export default CommitOptions;
